<template>
  <div class="layout">
    <Layout style="height: 100vh;">
      <Header>
        <top1></top1>
      </Header>
      <Layout>
        <Sider width="220" hide-trigger :style="{background: '#515a6e',position: 'fixed', height: '100vh', left: 0, 'overflow-y': 'scroll'}">
          <leftNavi></leftNavi>
        </Sider>
        <Layout :style="{'z-index':'100',padding: '0',marginLeft: '200px'}">
          <router-view></router-view>
        </Layout>
      </Layout>
    </Layout>
  </div>
</template>

<script>
  import top1 from '../components/top'
  import leftNavi from './leftNavi'
    export default {
        name: "myIframe"
      ,components:{
          top1
        ,leftNavi
      }
    }
</script>

<style scoped>
  .layout{
    /*border: 1px solid #d7dde4;*/
    background: #f5f7f9;
    position: relative;
    border-radius: 4px;
    overflow: hidden;

  }
</style>



