<template>
  <Menu theme="dark" width="auto" :active-name="myActive" ref="myActive" :open-names="['helper','1','2','3']">

    <Submenu name="helper">
      <template slot="title">
        <Icon type="ios-navigate"></Icon>帮助手册
      </template>
      <MenuItem name="main/es6" to="/main/es6">ES6帮助手册</MenuItem>
    </Submenu>

    <Submenu name="1">
      <template slot="title">
        <Icon type="ios-navigate"></Icon>基础
      </template>
      <MenuItem name="main/myButton" to="/main/myButton">Button</MenuItem>
    </Submenu>

    <Submenu name="2">
      <template slot="title">
        <Icon type="ios-keypad"></Icon>布局
      </template>
      <MenuItem name="main/Grid" to="/main/Grid">Grid 栅格</MenuItem>
      <MenuItem name="main/Card" to="/main/Card">Card 卡片</MenuItem>
    </Submenu>

    <Submenu name="3">
      <template slot="title">
        <Icon type="ios-analytics"></Icon>其他组件
      </template>
      <MenuItem name="main/myTable" to="/main/myTable">table 表格</MenuItem>
      <MenuItem name="main/myTree" to="/main/myTree">myTree 目录树</MenuItem>
      <MenuItem name="main/myTabs" to="/main/myTabs">myTabs 标签页</MenuItem>
      <MenuItem name="main/jsonServer" to="/main/jsonServer">jsonServer</MenuItem>

    </Submenu>
  </Menu>
</template>

<script>
  export default {
    name: "leftNavi"
    ,data(){
      return{
        myActive:""
      }
    },
    computed: {
      mainTabs: {
        get () { return this.$store.state.common.mainTabs },
        set (val) { this.$store.commit('common/updateMainTabs', val) }
      },
      mainTabsActiveName: {
        get () { return this.$store.state.common.mainTabsActiveName },
        set (val) { this.$store.commit('common/updateMainTabsActiveName', val) }
      }
    },
    watch: {
      $route: 'routeHandle'
    },
    methods:{
      //更新菜单
      upMenuActive () {
        this.$nextTick(()=>{
          this.myActive = this.$route.path.slice(1)
          console.info("---------------------------")
          console.info("进入左侧菜单 nextTick事件"+ Math.random())
          console.info("---------------------------")
          console.info("this.$route.path:"+this.$route.path)
          console.info("this.myActive:"+this.myActive)
          this.$refs.myActive.updateActiveName()
          console.info("this.$refs.myActive:"+this.$refs.myActive)
        })
      },
      routeHandle (route) {
        console.info("leftNavi.vue >>> watch route:"+route)
        this.upMenuActive()


        if (route.meta.isTab) {
          // tab选中, 不存在先添加
          console.info("route.name:"+route.name)
          let tab = this.mainTabs.filter(item => item.name === route.name)[0]
          console.info("tab:"+!tab)
          if (!tab) {
          //   if (route.meta.isDynamic) {
          //     route = this.dynamicMenuRoutes.filter(item => item.name === route.name)[0]
          //     if (!route) {
          //       return console.error('未能找到可用标签页!')
          //     }
          //   }
            tab = {
          //     menuId: route.meta.menuId || route.name,
              name: route.name,
          //     title: route.meta.title,
          //     type: isURL(route.meta.iframeUrl) ? 'iframe' : 'module',
          //     iframeUrl: route.meta.iframeUrl || '',
          //     params: route.params,
          //     query: route.query
            }
            this.mainTabs = this.mainTabs.concat(tab)
          }
          // this.menuActiveName = tab.menuId + ''
          this.mainTabsActiveName = tab.name
        }
      }
    },
    created(){
      //只有url输入时候 第一次加载 后面就不加载此事件了
      // this.upMenuActive()
      //
      this.routeHandle(this.$route)
    }
  }
</script>

<style scoped>
  a,a:hover {color:inherit;}
</style>



